import { Component } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { Store, select } from '@ngrx/store';

import { ColorSet, AppState, GroupColorSet, OutputMsg } from './service/interface';
import { StoreInfo, getColorSets, getGroupColor, getAppState } from './service/ngrx/storeInfo';
import { SAVE_STATE, REMOVE_STATE } from './components/one-color-set/one-color-set.component';
import {  AddColorSetAction, ModifyColorSetAction,
          RemoveColorSetAction } from './service/ngrx/set-reducer.service';
import {  SelectGroupColorSetStateAction,
          SelectSimpleColorSetStateAction } from './service/ngrx/app-state-reducer.service';
import {  AddNewGroupColorSet, RemoveGroupColorSet,
          ModifyGroupColorSet } from './service/ngrx/group-reducer.service';

import { Reducer } from './service/ngrx/set-reducer.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  colorSets: ColorSet[];
  groupColorSets: GroupColorSet[];
  appState: AppState;

  // 단일컬러셋 입력 폼
  oneColorSetForm: FormGroup = new FormGroup(
    {
      title: new FormControl(),
      colorValue: new FormControl(),
      text: new FormControl()
    }
  );

  constructor(private _store: Store<StoreInfo>) {
    this._store.select(getColorSets).subscribe(state => { this.colorSets = state; });
    this._store.select(getAppState).subscribe(state => { this.appState = state; });
    this._store.select(getGroupColor).subscribe(state => { this.groupColorSets = state; });
  }

  insert() {
    const colorSet: ColorSet = {  id: -1,
                                  title: this.oneColorSetForm.value.title,
                                  colorValue: { index: -1, value: this.oneColorSetForm.value.colorValue },
                                  text: this.oneColorSetForm.value.text };
    this._store.dispatch(new AddColorSetAction(colorSet));
    this.oneColorSetForm.reset();
  }

  insertNewGroupColorSet() {
    this._store.dispatch(new AddNewGroupColorSet());
    console.log(this.groupColorSets);
  }

  selectSCSState() {
    this._store.dispatch(new SelectSimpleColorSetStateAction());
  }

  selectGCSState() {
    this._store.dispatch(new SelectGroupColorSetStateAction());
  }

  private receiveOutput(event: OutputMsg) {
    switch (event.msg) {
      case SAVE_STATE:
        this._store.dispatch(new ModifyColorSetAction(event.value));
        break;

      case REMOVE_STATE:
        this._store.dispatch(new RemoveColorSetAction(event.value));
        break;
    }
  }

  private checkStore() {
    console.log(this._store);
  }

}



