import { BrowserModule } from '@angular/platform-browser';
import { ReactiveFormsModule } from '@angular/forms';
import { NgModule } from '@angular/core';
import { StoreModule } from '@ngrx/store';

import { AppComponent } from './app.component';
import { Reducer as ColorSetReducer } from './service/ngrx/set-reducer.service';
import { Reducer as AppStateReducer } from './service/ngrx/app-state-reducer.service';
import { Reducer as GroupColorSetReducer } from './service/ngrx/group-reducer.service';
import { ColorSetComponent } from './components/color-set/color-set.component';
import { OneColorSetComponent } from './components/one-color-set/one-color-set.component';
import { GroupColorSetComponent } from './components/group-color-set/group-color-set.component';


@NgModule({
  declarations: [
    AppComponent,
    ColorSetComponent,
    OneColorSetComponent,
    GroupColorSetComponent
  ],
  imports: [
    BrowserModule, ReactiveFormsModule,
    StoreModule.forRoot({ colorSets: ColorSetReducer,
                          appState: AppStateReducer,
                          groupColorSet: GroupColorSetReducer })
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



