import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

import { ColorSet, ColorValue, OutputMsg } from '../../service/interface';

export const SAVE_STATE = 'saveState';
export const REMOVE_STATE = 'removeState';

@Component({
  selector: 'app-one-color-set',
  templateUrl: './one-color-set.component.html',
  styleUrls: ['./one-color-set.component.scss']
})
export class OneColorSetComponent implements OnInit {
  @Output() output: EventEmitter<OutputMsg> = new EventEmitter();
  @Input('colorSet')
  set colorSet(value) {
    this._colorSet = JSON.parse(JSON.stringify(value));
    this.displayColorSet = JSON.parse(JSON.stringify(value));
  }
  get colorSet() {
    return this._colorSet;
  }

  _colorSet: ColorSet;
  displayColorSet: ColorSet;
  isEditMode = false;

  constructor() { }

  ngOnInit() {
  }

  receiveChangedColorValue(event: ColorValue) {
    this.displayColorSet.colorValue = event;
  }

  saveState() {
    console.log(JSON.stringify(this.displayColorSet));
    this.output.emit({ msg: SAVE_STATE, value: this.displayColorSet });
    this.isEditMode = false;
  }

  resetState() {
    console.log(JSON.stringify(this._colorSet));
    console.log(JSON.stringify(this.displayColorSet));
    this.displayColorSet = JSON.parse(JSON.stringify(this._colorSet));
    this.isEditMode = false;
  }

  removeState() {
    console.log(`removeState(): ${this.displayColorSet}`);
    this.output.emit({ msg: REMOVE_STATE, value: this.displayColorSet });
  }

}



