import { Component, OnInit, Input, Output, EventEmitter, ElementRef } from '@angular/core';

import { ColorValue } from '../../service/interface';
import { FormGroup, FormControl } from '@angular/forms';

@Component({
  selector: 'app-color-set',
  templateUrl: './color-set.component.html',
  styleUrls: ['./color-set.component.css']
})
export class ColorSetComponent implements OnInit {
  // @Input('colorValue')
  // set colorValue(value) {
  //   this._colorValue = value;
  //   // this.displayValue = value;
  // }
  // get colorValue() {
  //   return this._colorValue;
  // }

  @Input() colorValue: ColorValue = { index: -1, value: 'black' };
  @Input() index = -1;
  @Input() isEditMode = false;
  @Output() changedColorValue: EventEmitter<any> = new EventEmitter();

  constructor() { }

  ngOnInit() {
    this.colorValue.index = this.index;
    // console.log(`${this.index}: ${this.colorValue}`);
  }

  private outputChangedColorValue(ref) {
    // console.log(ref.value);
    const result: ColorValue = { index: this.index, value: ref.value };
    this.changedColorValue.emit(result);
  }

  // 부모에서 직접 호출 용도로 작성된 함수
  // public getColorValue(): ResultOfColorSet {
  //   return { index: this.index, colorValue: this.displayValue };
  // }
}



