import { GroupColorSet, ColorValue } from '../interface';
import { Action } from '@ngrx/store';

const ADD_NEW_GROUP_COLOR_SET = '[GROUP] addNewGroupColorSet';
const REMOVE_GROUP_COLOR_SET = '[GROUP] removeGroupColorSet';
const MODIFY_GROUP_COLOR_SET = '[GROUP] modifyGroupColorSet';

export class AddNewGroupColorSet implements Action {
  type = ADD_NEW_GROUP_COLOR_SET;
}

export class RemoveGroupColorSet implements Action {
  type = REMOVE_GROUP_COLOR_SET;
  constructor(public payload) {}
}

export class ModifyGroupColorSet implements Action {
  type = MODIFY_GROUP_COLOR_SET;
  constructor(public payload: GroupColorSet) {}
}

const initialState: GroupColorSet[] = [];

export function Reducer(state = initialState, action) {
  switch (action.type) {
    case ADD_NEW_GROUP_COLOR_SET:
      const id = state.length === 0 ? 0 : state[state.length - 1].id + 1;
      const newGroup: GroupColorSet = { id: id,
                                        title: '',
                                        text: '',
                                        colorValue: [ { index: -1, value: 'black' },
                                                      { index: -1, value: 'black' },
                                                      { index: -1, value: 'black' },
                                                      { index: -1, value: 'black' },
                                                      { index: -1, value: 'black' }] };
      return [...state, newGroup];

    case REMOVE_GROUP_COLOR_SET:
      return state.filter(item => {
        return item.id !== action.payload.id;
      });

    case MODIFY_GROUP_COLOR_SET:
      const payload: GroupColorSet = action.payload;
      return state.map(item => {
        return item.id === payload.id ? Object.assign({}, item, payload) : item;
      });

    default:
      return state;
  }
}



