import { Component, OnInit, Input } from '@angular/core';
import { ViewChildren, QueryList } from '@angular/core';
import { Store } from '@ngrx/store';

import { StoreInfo } from '../../service/ngrx/storeInfo';
import { GroupColorSet, ColorValue } from '../../service/interface';
import { ColorSetComponent } from '../color-set/color-set.component';
import { ModifyGroupColorSet, RemoveGroupColorSet } from '../../service/ngrx/group-reducer.service';

@Component({
  selector: 'app-group-color-set',
  templateUrl: './group-color-set.component.html',
  styleUrls: ['./group-color-set.component.css']
})
export class GroupColorSetComponent implements OnInit {
  // 사용하진 않지만, 그냥 예제겸 남김
  // @ViewChildren('app-color-set') colorSets: QueryList<ColorSetComponent>;

  @Input('groupColorSet')
  set groupColorSet(value: GroupColorSet) {
    this._groupColorSet = JSON.parse(JSON.stringify(value));
    this.displayGroupColorSet = JSON.parse(JSON.stringify(value));
    // console.log(this._groupColorSet);
    // console.log(this.displayGroupColorSet);
  }

  get groupColorSet() {
    return this._groupColorSet;
  }

  _groupColorSet: GroupColorSet;
  displayGroupColorSet: GroupColorSet;
  isEditMode = false;

  constructor(private _store: Store<StoreInfo>) { }

  ngOnInit() {
  }

  private receiveChangedColorValue(event: ColorValue) {
    this.displayGroupColorSet.colorValue[event.index] = event;
    console.log(`group-color-set.component.ts: Child component로부터 ChangedColorValue(${JSON.stringify(event)})를 받았습니다.`);

    console.log(`${this.displayGroupColorSet.colorValue}`);
  }

  trackByFn(index, item) {
    return index;
  }

  private saveState() {
    this._store.dispatch(new ModifyGroupColorSet(this.displayGroupColorSet));
    this.isEditMode = false;
  }

  // 취소 버튼을 누르면 원본 값으로 초기화 해줘요.
  private resetState() {
    this.displayGroupColorSet = JSON.parse(JSON.stringify(this._groupColorSet));
    this.isEditMode = false;
  }

  private removeState() {
    this._store.dispatch(new RemoveGroupColorSet(this.displayGroupColorSet));
    this.isEditMode = false;
  }

  // 자식 컴포넌트의 함수를 사용하는 예제겸 남김
  // "ColorSetComponent"의 내부 값({ index:number, colorValue: string })을 가져와요.
  // getValueFromColorSets() {
  //   for (let i = 0; i < this.colorSets.toArray().length; i++) {
  //     console.log(this.colorSets.toArray()[i].getColorValue());
  //   }
  // }

}



