import { Action } from '@ngrx/store';
import { AppState } from '../interface';

export const SELECT_SIMPLE_COLOR_SET_STATE = '[APP_STATE] selectSimpleColorSetState';
export const SELECT_GROUP_COLOR_SET_STATE = '[APP_STATE] selectGroupColorSetState';

const initialState: AppState =  { isSimpleColorSetShowed: true,
                                  isGroupColorSetShowed: false };

export class SelectSimpleColorSetStateAction implements Action {
  type = SELECT_SIMPLE_COLOR_SET_STATE;
}

export class SelectGroupColorSetStateAction implements Action {
  type = SELECT_GROUP_COLOR_SET_STATE;
}

export function Reducer(state = initialState, action) {
  const appState: AppState = {  isSimpleColorSetShowed: true,
                                isGroupColorSetShowed: false };
  switch (action.type) {
    case SELECT_SIMPLE_COLOR_SET_STATE:
      return Object.assign({}, state, appState);

    case SELECT_GROUP_COLOR_SET_STATE:
      appState.isSimpleColorSetShowed = false;
      appState.isGroupColorSetShowed = true;
      return Object.assign({}, state, appState);

    default:
      return state;
  }
}



