import { AppState, ColorSet, GroupColorSet } from '../interface';


export interface StoreInfo {
  colorSets: ColorSet[];
  groupColorSet: GroupColorSet[];
  appState: AppState;
}

// 아마도 module에 등록된 리듀서의 객체가 'redusers'를 통해 들어오는지도 모르겠다.
export function getColorSets(redusers: StoreInfo) {
  return redusers.colorSets;
}

export function getGroupColor(redusers: StoreInfo) {
  return redusers.groupColorSet;
}

export function getAppState(redusers: StoreInfo) {
  return redusers.appState;
}



