import { Action } from '@ngrx/store';

import { ColorSet } from '../interface';

export const ADD_COLOR_SET = 'add_color_set';
export const MODIFY_COLOR_SET = 'modify_color_set';
export const REMOVE_COLOR_SET = 'remove_color_set';

const initialState: ColorSet[] = [];

export class AddColorSetAction implements Action {
  type = ADD_COLOR_SET;
  constructor(public payload: ColorSet) {}
}

export class ModifyColorSetAction implements Action {
  type = MODIFY_COLOR_SET;
  constructor(public payload: ColorSet) {}
}

export class RemoveColorSetAction implements Action {
  type = REMOVE_COLOR_SET;
  constructor(public payload: ColorSet) {}
}

export const Reducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_COLOR_SET:
      const id = state.length === 0 ? 0 : state[state.length - 1].id + 1;
      action.payload.id = id;
      return [ ...state, action.payload ];

    case MODIFY_COLOR_SET:
      return state.map(item => {
        return item.id === action.payload.id ? action.payload : item;
        // if (item.id === action.payload.id) {
        //   return action.payload;
        // }
      });

    case REMOVE_COLOR_SET:
      return state.filter(color_set => {
        return color_set.id !== action.payload.id;
        }
      );

    default:
      return state;
  }

};



